<style scoped lang="stylus" rel="stylesheet/stylus">
.container
    background #f2f2f2
    min-height 100%
    &>div
        border-bottom 1px solid #ccc
        .title
            font-size 15px
            color #333
            text-align center
            font-weight bold
        .tip
            font-size 14px
            color #888
</style>

<template>
  <div class="container">
    <div>
        <div class="title">过滤器</div>
        时间：{{ date | formatDate }}
    </div>
    <div>
        <div class="title">计算属性set,get</div>
        姓：{{ firstName }}
        名：{{ lastName }}
        姓名：{{ fullName }}
        <!--<van-field type="text" v-model="fullName"></van-field>-->
        <input type="text" v-model="fullName"></input>
    </div>
    <div>
        <div class="title">class绑定</div>
        <div :class="{ 'active': isActive, 'error': isError }">对象语法</div>
        <div :class="[activeCls, errorCls, isVisited ? visitedCls : '']">数组语法</div>
        <div :class="[{ 'active': isActive, 'error': isError }, visitedCls]">数组语法中使用对象语法</div>
        <div :class="computedClasses">数组语法中使用对象语法-计算属性</div>
    </div>
    <div>
        <div class="title">style绑定</div>
        <div :style="{ 'color': color, 'font-weight': fontWeight, 'fontSize': fontSize }">对象语法</div>
        <div :style="[styleA, styleB]">数组语法</div>
    </div>
    <div>
        <div class="title">条件渲染指令</div>
        <div class="tip">Vue 在渲染元素时 ，出于效率考虑，会尽可能地复用已有的元素而非重新渲染,加唯一值的key属性可以避免。v-show不能在template上使用</div>
        <template v-if="type === 'name'">
            <label>用户名</label>
            <input type="text" placeholder="请输入用户名" key="name-input" />
        </template>
        <template v-else>
            <label>邮箱</label>
            <input type="text" placeholder="请输入邮箱" key="mail-input" />
        </template>
        <button @click="toggleType">切换输入类型</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'vuejswar',
  components: {
  },
  props: {
  },
  data() {
    return {
      date: new Date(),

      firstName: 'Jack',
      lastName: 'Green',

      isActive: true,
      isError: false,
      activeCls: 'active',
      errorCls: 'error',
      isVisited: true,
      visitedCls: 'visited',
      btnSize: 'large',
      disabled: true,

      color: 'blue',
      fontWeight: 'bold',
      fontSize: '20px',
      styleA: {
        'color': 'red',
        'font-size': '18px',
      },
      styleB: {
        'font-weight': 'bold',
      },

      type: 'name',
    };
  },
  filters: {
    formatDate(value) {
      let date = new Date(value);
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDay();
      return `${year}-${month}-${day}`;
    },
  },
  computed: {
    //    fullName() {
    //        return `${this.firstName} ${this.lastName}`
    //    },
    fullName: {
      get() {
        return `${this.firstName} ${this.lastName}`;
      },
      set(newValue) {
        this.firstName = newValue.split(' ')[0];
        this.lastName = newValue.split(' ')[1];
      },
    },
    computedClasses() {
      return [
        'btn',
        {
          ['btn-' + this.btnSize]: this.size !== '',
          'btn-disabled': this.disabled,
        },
      ];
    },
  },
  methods: {
    toggleType() {
      this.type = this.type === 'name' ? 'mail' : 'name';
    },
  },
};
</script>



